.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input {
  border: 4px solid black;
  /* border-image: linear-gradient(90deg, #ff8a00, #e52e71); /* gradient colors */
  /* border-image-slice:1; required for the image to appear */ 
  padding: 8px;
  border-radius: 15px;
  margin: 5px;
  /* border-radius: 5px; */
}
button{
  margin: 5px;
}